window.addEventListener('DOMContentLoaded', event => {

    // Toggle the side navigation
    const sidebarToggle = document.body.querySelector('#sidebarToggle');
    if (sidebarToggle) {
        // Uncomment Below to persist sidebar toggle between refreshes
        // if (localStorage.getItem('sb|sidebar-toggle') === 'true') {
        //     document.body.classList.toggle('sb-sidenav-toggled');
        // }
        sidebarToggle.addEventListener('click', event => {
            event.preventDefault();
            document.body.classList.toggle('sb-sidenav-toggled');
            localStorage.setItem('sb|sidebar-toggle', document.body.classList.contains('sb-sidenav-toggled').toString());
        });
    }
    const dapp = new DAPP();
    window["dapp"] = dapp;
});

import { ethers } from 'ethers';
import $ from 'jquery';
import { ExternalProvider } from "@ethersproject/providers";

declare global {
    interface Window {
        ethereum?: ExternalProvider;
    }
}

class DAPP {

    private signer;
    private provider;
    private accs;
    private contract;

    constructor() {
        this.initEth();
    }

    hello () {
        alert("Hello world!");
    }

    async initEth() {
        if (window.ethereum == null || window.ethereum == undefined) {
            console.log("no eth");
        } else {
            if (window.ethereum !== undefined && window.ethereum.request !== undefined) {
                this.accs = await window.ethereum.request({
                    method: 'eth_requestAccounts',
                    params: [],
                });
    
                console.log(this.accs);
        
                this.provider = new ethers.providers.Web3Provider(window.ethereum);
                this.signer = await this.provider.getSigner();
        
                const { chainId } = await this.provider.getNetwork();
                if (chainId != 56) {
                    await window.ethereum.request({
                        method: "wallet_addEthereumChain",
                        params: [{
                            chainId: "0x38",
                            rpcUrls: ["https://bsc-dataseed.binance.org"],
                            chainName: "BSC Mainnet",
                            nativeCurrency: {
                                name: "BNB",
                                symbol: "BNB",
                                decimals: 18
                            },
                            blockExplorerUrls: ["https://bscscan.com"]
                        }]
                    });
                    window.location.href = "./";
                }
            }
        }
    }

}